.is-long-text {
  @include long-text-style(.9375rem, .8125rem);
  p {
    code {
      background-color: #f0f0f0; } }
  .highlight {
    @include source-code-pro; }
  pre[class*=language-] {
    padding: 0; }
  label {
    @include text-block(.9375rem 1.5 .2rem, 700 block); }
  textarea,
  input[type="text"] {
    display: block;
    border: 1px solid $text-color;
    width: 100%;
    margin-bottom: 1.5rem;
    padding: .5rem;
    @include text-block(.875rem 1.8 $text-color); }
  input[type="submit"] {
    border: 1px solid $text-color;
    padding: .5rem 1rem;
    margin-bottom: 1.5rem; }
  iframe {
    max-width: 100%;
    margin-bottom: 2em;
    display: block;
    @include margin(horizontal, auto);
    &.speakerdeck-iframe {
      margin-bottom: 2em !important; } }
  .amazlet-box:last-child {
    margin-top: 4rem;
    .amazlet-image img {
      width: 10rem; } } }



